  methods: {
    toggleTheme() {
      this.isDarkTheme = !this.isDarkTheme;
      document.documentElement.setAttribute('data-theme', this.isDarkTheme ? 'dark' : 'light');
      localStorage.setItem('theme', this.isDarkTheme ? 'dark' : 'light');
    },
    debounce(fn, delay) {
      let timeout;
      return function (...args) {
        clearTimeout(timeout);
        timeout = setTimeout(() => fn.apply(this, args), delay);
      };
    },
    collapseTimeline() {
      // let sectionElement = document.querySelectorAll('.section')[1];
      // this.isCollapsed ? sectionElement.classList.remove('collapsed') : sectionElement.classList.add('collapsed');
      let button = document.querySelector('button.collapser')

      this.isCollapsed ? button.innerText = 'See Less' : button.innerText = 'See All';
      this.showAllProjects = !this.showAllProjects;
      this.isCollapsed = !this.isCollapsed

      const resizeEvent = new Event('resize', { bubbles: true, cancelable: true });
      window.dispatchEvent(resizeEvent);
      window.scrollTo(0,0)
      // this.calculateSectionBounds(this.sectionBounds);

    },
    setupTimeline() {
      // create an array that automatically goes from 2017
      // (my oldest project) to the current year

      const d = new Date();
      let maxnum = d.getFullYear() - 2000
      let years = []
      let usedYears = []

      for (let i = 17; i <= maxnum; i++) {
        years.push(i)
      }
      years.reverse(); // eg: [25, 24, 23, 22, 21, 20, 19, 18, 17]

      let yearHeads = [] // first element of each of the years (latest project of that year)

      for (let year of years) {
        let head = document.querySelector(`.proj_year_${year}`)
        if (head) {
          yearHeads.push(head); usedYears.push(year)
        } else continue;
      }
      let topBoundingValues = []
      for (let firstYearElm of yearHeads) {
        topBoundingValues.push(firstYearElm.getBoundingClientRect().top)
      }

      let heightRectValues = []
      for (let value of topBoundingValues) {
        let next = topBoundingValues[topBoundingValues.indexOf(value) + 1]
        if (next) {
          heightRectValues.push(next - value - 48)
        }
      }
      // now begin creating year cards and vertical lines
      let finalOffset = 0
      let yearParent = document.querySelector(".timeline-years")
      yearParent.innerHTML = ""
      for (let i in yearHeads) {
        let head = document.createElement("span");
        let vl = document.createElement("div");
        vl.classList = 'vl';
        finalOffset += 48;

        if (!heightRectValues[i]) {
          let lastHeight = document.querySelector(".project-container").getBoundingClientRect().height - finalOffset
          vl.style.height = `${lastHeight}px`
        } else {
          finalOffset += heightRectValues[i]
        }
        head.innerText = `20${usedYears[i]}`
        vl.style.height = `${heightRectValues[i]}px`
        yearParent.appendChild(head)
        yearParent.appendChild(vl)

      } 
    },
    calculateSectionBounds(sectionBounds) {
      sectionBounds.splice(0, sectionBounds.length)
      let sections = document.querySelectorAll(".section")

      for (let section of sections) {
        sectionBounds.push(section.getBoundingClientRect().y + window.scrollY - 200)
      }
      sectionBounds.shift();
      sectionBounds.push(document.documentElement.scrollHeight)
    }
  },
  created() {
    this.debouncedSetupTimeline = this.debounce(this.setupTimeline, 100);
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.debouncedSetupTimeline);
  },
  async mounted() {
    // Initialize theme
    document.documentElement.setAttribute('data-theme', this.isDarkTheme ? 'dark' : 'light');
    
    ////////////////////////////  
    // NAVIGATION
    ////////////////////////////
    let navParent = document.querySelector(".navigation")

    function clearChildrenStyles(parent) {
      for (let child of parent.children) {
        child.classList = ''
      }
    }

    this.$nextTick(() => {
      setTimeout(() => {
        this.calculateSectionBounds(this.sectionBounds)
      }, 100);
    });
      
    window.addEventListener('scroll', () => {
      if(!this.showAllProjects)
      {
        // navParent = document.querySelector(".navigation")
        let scrollValue = window.scrollY;
        // console.log(scrollValue, navParent)
        for (let i in this.sectionBounds) {
          if (scrollValue < this.sectionBounds[i] - 1) { // -1 should not be ideal but 
                    // safari doesnt replicate the same behaviour as chrome; hence compensating

            clearChildrenStyles(navParent)
            navParent.children[i].classList = "nav-active"
            break
          }
        }
      }
      else
      {
        clearChildrenStyles(navParent)
        navParent.children[1].classList = "nav-active"
      }
      })
    
    for (let i = 0; i < navParent.childElementCount; i++) {
      navParent.children[i].addEventListener('click', () => {
        if(this.showAllProjects) 
          document.querySelector("button.collapser").click();

        window.scrollTo(0, this.sectionBounds[i - 1])
      })
    }
    ////////////////////////////
    /////// Timeline js ////////
    ////////////////////////////
    this.$nextTick(() => {
      setTimeout(() => this.setupTimeline(), 100);
    });
    window.addEventListener("resize", this.debouncedSetupTimeline);

    function animateUp(query)
    {
      // grab all children except fun facts because it screws up the blur fade in animations
      let parent = document.querySelectorAll(`${query} > *:not(.fun-fact)`);
      parent.forEach(async (el, i) => {
        el.style.opacity = 0;
        el.style.animation = 'fadeInUp 0.5s ease forwards';
        el.style.animationDelay = `${0.05 * i}s`
        await new Promise(r => setTimeout(r, 700));
        // reset the element properties that i changed
        el.style.animationFillMode = 'none'
        el.style.animation = 'none'
        el.style.opacity = 1;
      })
    }

    // add some spicy animations
    animateUp(".left")
    animateUp(".right") 
    animateUp(".intro") 
    animateUp(".connections") 
    // animateUp(".navigation") 

    /*
    ok so the timeline has a slight offset glitch.
    basically, when my video or images still have not loaded,
    the timeline still gets set up. this sets it to a shorter height
    because the resources havent loaded yet.
    then the resources gets loaded and pushes all the elements down
    (because each has some height)

    this causes the timeline to be offset each time when the resources
    havent been cached yet and are still loading.

    lets fix it by waiting for the resource load and for each resource
    load i resetup the timeline offsets (this.debounceSetupTimeline)
    */

    let resourceElms = document.querySelectorAll(".project-header > *:not(div,p)");
    resourceElms.forEach((elm) => {
      elm.addEventListener("load", this.debouncedSetupTimeline);
    });
    
  }
}
